<template>
  <div class="sn-page homepage" :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/sy_icon_background.png')`}">
    <!-- 标题 -->
    <div class="title-introduce" >
      <img :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/sy_icon_title.png'" alt="">
      <div class="introduce" v-html="sub"></div>
    </div>

    <!-- 入口按钮 -->
    <div class="btn-box" >
      <ul class="sn-f-s-c btn-ul" v-if="functionList && functionList.length">
        <li class="sn-f-s-c single" v-for="(obj, i) in functionList" :key="i" :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/sy_background_function.png')` }"
          @click="funGoPath(i)">
          <div class="icon-box">
            <img :src="ALI_CLOUD_DOMAIN_STATIC + obj.main">
          </div>
          <div class="name">{{ obj.title }}</div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script type="text/javascript">
// id 来串后面业务流程
// main 第一张屏图标
// sub 第二张屏
// enable 是否启用此功能
// sort 排序
// suffix 是否需要调后续 js 文件
import * as constants from '../../common/utils/constants';

export default {
  data() {
    return {
      ALI_CLOUD_DOMAIN_STATIC: constants.ALI_CLOUD_DOMAIN_STATIC, // 静态路径
      sub: '为医院、医生、医药、医保等行业客户提供互联网医疗服务解决方案的科技服务型公司！', // 小标题
      errRequestNum: 0, // 失败请求次数
      functionList: [
        // {
        //   'title': '首页',
        //   'main': '',
        //   'sub': '/hos/dashboard/images/description/sub_icon_sy.png',
        //   'id': 'sy',
        //   'enable': '1',
        //   'sort': '0',
        //   'suffix': '0',
        //   'remark': ''
        // }, {
        //   'title': '公司介绍',
        //   'main': '/hos/dashboard/images/description/main_icon_gsjs.png',
        //   'sub': '/hos/dashboard/images/description/sub_icon_gsjs.png',
        //   'id': 'gsjs',
        //   'enable': '1',
        //   'sort': '1',
        //   'suffix': '1',
        //   'remark': ''
        // }, {
        //   'title': '团队介绍',
        //   'main': '/hos/dashboard/images/description/main_icon_tdjs.png',
        //   'sub': '/hos/dashboard/images/description/sub_icon_tdjs.png',
        //   'id': 'tdjs',
        //   'enable': '1',
        //   'sort': '2',
        //   'suffix': '1',
        //   'remark': ''
        // }, {
        //   'title': '发展历程',
        //   'main': '/hos/dashboard/images/description/main_icon_fzlc.png',
        //   'sub': '/hos/dashboard/images/description/sub_icon_fzlc.png',
        //   'id': 'fzlc',
        //   'enable': '1',
        //   'sort': '3',
        //   'suffix': '1',
        //   'remark': ''
        // }, {
        //   'title': '公司战略',
        //   'main': '/hos/dashboard/images/description/main_icon_gszl.png',
        //   'sub': '/hos/dashboard/images/description/sub_icon_gszl.png',
        //   'id': 'gszl',
        //   'enable': '1',
        //   'sort': '4',
        //   'suffix': '1',
        //   'remark': ''
        // }, {
        //   'title': '产业结构',
        //   'main': '/hos/dashboard/images/description/main_icon_cyjg.png',
        //   'sub': '/hos/dashboard/images/description/sub_icon_cyjg.png',
        //   'id': 'cyjg',
        //   'enable': '1',
        //   'sort': '5',
        //   'suffix': '1',
        //   'remark': ''
        // }, {
        //   'title': '公司荣誉',
        //   'main': '/hos/dashboard/images/description/main_icon_gsry.png',
        //   'sub': '/hos/dashboard/images/description/sub_icon_gsry.png',
        //   'id': 'gsry',
        //   'enable': '1',
        //   'sort': '6',
        //   'suffix': '1',
        //   'remark': ''
        // }
      ]
    };
  },

  created() {
    this.funLoadData();
  },

  methods: {
    /*
    * 2023-04-13 16:26:26 Ywr
    * 初始化数据
    */
    funLoadData() {
      this.errRequestNum++;
      const params = {
        domain: this.ALI_CLOUD_DOMAIN_STATIC,
        url: `/hos/dashboard/caption/description/function.js?time=${this.$sn.format.date(this.$sn.utils.getCurrentTime(), 'yyyyMMddHHmmss')}`
      };
      this.$sn.utils.ordinaryRequest(
        params,
        () => {},
        () => {
          // 可请求三次
          if (this.functionList.length === 0 & this.errRequestNum < 3) {
            this.funLoadData();
          }
        }
      ).then(res => {
        // 筛选开启的功能
        let List = res.function.filter(el => {
          return el.enable === '1';
        });
        // 排序
        List = List.sort((a, b) => {
          return +a.sort - (+b.sort);
        });
        // 赋值
        this.functionList = List.slice(1);
        this.sub = res.sub;
      });
    },

    /*
    * 2023-04-03 09:16:18 Ywr
    * 路由跳转
    */
    funGoPath(index) {
      this.$router.push({ path: `/introduce?index=${index}` });
    }
  },

  beforeDestroy() {
  }
};
</script>

<style lang="scss">
.homepage {
  background-size: cover;
  background-repeat: no-repeat;

  // 标题 + 介绍
  .title-introduce {
    margin-top: 120px;
    margin-left: 119px;

    img {
      width: 564px;
      height: 133px;
    }

    .introduce {
      margin-top: 47px;
      width: 989px;
      font-size: 35px;
      line-height: 52.5px;
      font-weight: 400;
      color: $white;
    }
  }

  // 入口按钮
  .btn-box {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 1920px;
    // height: 497px;
    border-top: 2px solid $border;
    backdrop-filter: blur(15px);

    .btn-ul {
      max-width: 1170px;
      flex-wrap: wrap;
      margin-left: 119px;
      margin-top: 50px;
      margin-bottom: 21px;

      .single {
        width: 354px;
        height: 111px;
        background-size: cover;
        background-repeat: no-repeat;
        margin-right: 34px;
        margin-bottom: 29px;

        .icon-box {
          margin: 0 45px 0 56px;

          img {
            width: 51px;
            height: fit-content;
          }
        }

        .name {
          color: $white;
          font-size: 32px;
          line-height: 48px;
          font-weight: bold;
        }
      }
    }
  }
}
</style>
